<template>
  <span/>
</template>

<script>
    import { CountUp } from '../../libs/countUp';

    const typeOf = type => object => Object.prototype.toString.call(object) === `[object ${type}]`;
    // const isString = typeOf('String');
    // const isObject = typeOf('Object');
    const isFunction = typeOf('Function');
    export default {
        name: 'CountUp',
        props: {
            delay: {
                type: Number,
                required: false,
                default: 0,
            },
            endVal: {
                type: Number|String,
                required: true,
            },
            updateVal: {
                type: Number|String,
                required: false,
            },
            options: {
                type: Object,
                required: false,
            },
        },
        data() {
            return {
                instance: null,
            };
        },
        // computed: {},
        watch: {
            endVal: {
                handler(value) {
                    const that = this;
                    if (that.instance && isFunction(that.instance.update)) {
                        that.instance.update(value);
                    }
                },
                deep: false
            },
        },
        methods: {
            create() {
                const that = this;
                if (that.instance) {
                    return;
                }
                const dom = that.$el;
                const instance = new CountUp(
                    dom,
                    that.endVal,
                    that.options
                );
                if (instance.error) {
                    // error
                    return;
                }
                that.instance = instance;
                if (that.delay < 0) {
                    that.$emit('ready', this, CountUp);
                    return;
                }
                setTimeout(() => instance.start(() => that.$emit('ready', this, CountUp)), that.delay);
            },
            destroy() {
                const that = this;
                that.instance = null;
            },
            printValue(value) {
                const that = this;
                if (that.instance && isFunction(that.instance.printValue)) {
                    return that.instance.printValue(value);
                }
            },
            start(callback) {
                const that = this;
                that.instance = null;
                that.create();
                /*const that = this;
                if (that.instance && isFunction(that.instance.start)) {
                    return that.instance.start(callback);
                }*/
            },
            pauseResume() {
                const that = this;
                if (that.instance && isFunction(that.instance.pauseResume)) {
                    return that.instance.pauseResume();
                }
            },
            reset() {
                const that = this;
                if (that.instance && isFunction(that.instance.reset)) {
                    return that.instance.reset();
                }
            },
            update(newEndVal) {
                const that = this;
                if (that.instance && isFunction(that.instance.update)) {
                    return that.instance.update(newEndVal);
                }
            }
        },
        mounted() {
            const that = this;
            that.create();
        },
        beforeDestroy() {
            const that = this;
            that.destroy();
        },
    };
</script>



